* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #282836;
  width: 100vw;
  height: 100dvh;
}
@media only screen and (min-width: 992px) {
  .main-content {
    z-index: 5;
    position: absolute;
    width: 100vw;
    height: 100dvh;
    display: grid;
    grid-template-rows: 10dvh 80dvh 10dvh;
    place-items: center;
    .logo {
      width: 9rem;
      user-select: none;
      margin-top: 1rem;
    }
    .title {
      grid-template-rows: 1/2;
      font-family: "Bebas Neue", sans-serif;
      color: white;
      text-align: center;
      font-size: 17rem;
      line-height: 13rem;
      user-select: none;
    }
    #instagram-logo {
      font-size: 2rem;
      color: white;
    }
  }
  .background-container {
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: relative;
    #blue--dot {
      width: 80vw;
      position: absolute;
      bottom: -120dvh;
      left: -17vw;
    }
    #green--dot {
      z-index: 3;
      position: absolute;
      width: 40vw;
      bottom: -20dvh;
      right: -10vw;
      transform: rotate(279deg);
    }
    #red--dot {
      z-index: 1;
      position: absolute;
      width: 60vw;
      right: -25vw;
      top: -55dvh;
      transform: rotate(200deg);
    }
    #pink--dot {
      z-index: 0;
      width: 35vw;
      position: absolute;
      top: -30dvh;
      left: -10vw;
      transform: rotate(60deg);
    }
    #white--dot {
      z-index: 5;
      position: absolute;
      width: 25vw;
      bottom: 30dvh;
      right: -10vw;
    }
    #yellow--dot {
      z-index: 3;
      position: absolute;
      width: 40vw;
      bottom: -40dvh;
      right: 7vw;
      transform: rotate(270deg);
    }
  }
}

@media only screen and (max-width: 600px) {
  .main-content {
    z-index: 5;
    position: absolute;
    width: 100vw;
    height: 100dvh;
    display: grid;
    grid-template-rows: 10dvh 80dvh 10dvh;
    place-items: center;
    .logo {
      width: 9rem;
