      text-align: center;
      font-size: 8rem;
      line-height: 6rem;
      user-select: none;
    }
    #instagram-logo {
      font-size: 2rem;
      color: #282836;
    }
  }
  .background-container {
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: relative;
    #blue--dot {
      width: 10vw;
      display: none;
    }
    #green--dot {
      position: absolute;
      width: 70vw;
      top: 6dvh;
      left: -45vw;
      transform: rotate(45deg);
    }
    #red--dot {
      width: 10vw;
      display: none;
    }
    #pink--dot {
      z-index: 3;
      width: 130vw;
      position: absolute;
      bottom: 10dvh;
      right: -90vw;
      transform: rotate(290deg);
    }
    #white--dot {
      z-index: 2;
      width: 10vw;
      position: absolute;
      width: 90vw;
      bottom: -30dvh;
      left: -50vw;
    }
    #yellow--dot {
      position: absolute;
      width: 120vw;
      bottom: -30dvh;
      left: 0;
      transform: translate(-10vw) rotate(270deg);
    }
  }
}

